<style lang="css" scoped>
.el-header {
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  line-height: 60px;
}
.el-select {
  width: 120px;
}
</style>
<template>
  <el-row>
    <el-col :span="24" :offset="0">
      <el-container>
        
        <el-main>
           <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="Beauty" name="first">
              <el-col :span="12" :offset="6" style="margin-top:30px;margin-bottom:70px;">
                <el-input placeholder="请输入ASIN或者Title" v-model="keyword" class="input-with-select">
                  <el-select v-model="country" slot="prepend" placeholder="请选择">
                    <el-option label="美国" value="1"></el-option>
                    <el-option label="英国" value="2"></el-option>
                    <el-option label="日本" value="3"></el-option>
                    <el-option label="加拿大" value="4"></el-option>
                    <el-option label="德国" value="5"></el-option>
                    <el-option label="法国" value="6"></el-option>
                    <el-option label="西班牙" value="7"></el-option>
                    <el-option label="意大利" value="8"></el-option>
                  </el-select>
                  <el-button slot="append" icon="el-icon-search" @click="BeautySearch()"></el-button>
                </el-input>
              </el-col>
              <el-table
              v-loading="loading"
              :data="tableData"
              :header-cell-style="TbHeaderStyle"
              style="width: 100%">
              <el-table-column
              prop="asin"
              align="center"
              fit
              label="Asin">
              </el-table-column>
              <el-table-column
              prop="Title"
              label="Title"
              width="400">
              </el-table-column>
              <el-table-column
              prop="Rank"
              align="center"
              label="Rank">
              </el-table-column>
              <el-table-column
              prop="Impressions"
              align="center"
              width="100"
              label="Impressior">
              </el-table-column>
              <el-table-column
              prop="Clicks"
              align="center"
              label="Clicks">
              </el-table-column>
              <el-table-column
              prop="Adds"
              align="center"
              label="Adds">
              </el-table-column>
              <el-table-column
              prop="UnitsOrdered"
              align="center"
              width="120"
              label="Units Ordered">
              </el-table-column>
              <el-table-column
              prop="Price"
              align="center"
              label="Price">
              </el-table-column>
              <el-table-column
              prop="AvgReviewRating"
              align="center"
              label="Avg. Review Rating">
              </el-table-column>
              <el-table-column
              prop="ReviewCount"
              align="center"
              width="120"
              label="Review Count">
              </el-table-column>
              <el-table-column
              prop="ProductAvailableDay"
              align="center"
              label="Product Available Day">
              </el-table-column>
              <el-table-column
              prop="3POffers"
              align="center"
              label="3POffers">
              </el-table-column>
              </el-table>
            </el-tab-pane>

            <el-tab-pane label="类目搜索词" name="second">
              <el-col :span="12" :offset="6" style="margin-top:30px;margin-bottom:70px;">
                
                <el-row>
                  <el-col :span="16" >
                    <el-input placeholder="请输入关键词" v-model="keyword1" class="input-with-select">
                      <el-select v-model="country" slot="prepend" placeholder="请选择">
                        <el-option label="美国" value="1"></el-option>
                        <el-option label="英国" value="2"></el-option>
                        <el-option label="日本" value="3"></el-option>
                        <el-option label="加拿大" value="4"></el-option>
                        <el-option label="德国" value="5"></el-option>
                        <el-option label="法国" value="6"></el-option>
                        <el-option label="西班牙" value="7"></el-option>
                        <el-option label="意大利" value="8"></el-option>
                      </el-select>
                      <el-button slot="append" icon="el-icon-search" @click="KeywdSearch()"></el-button>
                    </el-input>
                  </el-col>
                  <el-col :span="6" :offset="1">
                    <el-switch
                      v-model="precise"
                      active-color="#13ce66"
                      inactive-color="#ff4949"
                      active-text="精确搜索"
                      inactive-text="模糊搜索">
                    </el-switch>
                  </el-col>
                </el-row>
              </el-col>
              <el-table
              :data="tableData1" 
              v-loading="loading1"
              :header-cell-style="TbHeaderStyle"
              style="width: 100%">
              <el-table-column
              prop="Keywords"
              align="center"
              label="Keywords">
              </el-table-column>
              <el-table-column
              prop="Rank"
              align="center"
              label="Rank">
              </el-table-column>
              <el-table-column
              prop="QueryGroupCountDifference"
              align="center"
              label="Query Group Count Difference">
              </el-table-column>
              <el-table-column
              prop="Current Query Group Count"
              align="center"
              label="Current Query Group Count">
              </el-table-column>
              <el-table-column
              prop="Previous Query Group Count"
              align="center"
              label="Previous Query Group Count">
              </el-table-column>
              <el-table-column
              prop="CurrentSearchCount"
              align="center"
              label="Current Search Count">
              </el-table-column>
              <el-table-column
              prop="Previous Search Count"
              align="center"
              label="Previous Search Count">
              </el-table-column>
              <el-table-column
              prop="Query Group Percent Change (%)"
              align="center"
              label="Query Group Percent Change (%)">
              </el-table-column>
              <el-table-column
              prop="Current Click Count"
              align="center"
              label="Current Click Count">
              </el-table-column>
              <el-table-column
              prop="Current Add Count"
              align="center"
              label="Current Add Count">
              </el-table-column>
              <el-table-column
              prop="Current Click Rate (%)"
              align="center"
              label="Current Click Rate (%)">
              </el-table-column>
              <el-table-column
              prop="Current Add Rate (%)"
              align="center"
              label="Current Add Rate (%)">
              </el-table-column>
              <el-table-column
              prop="Searchumb"
              align="center"
              label="List Quantity">
              </el-table-column>
              <el-table-column
              prop="SearchFbaumb"
              align="center"
              label="FBA List Quantity">
              </el-table-column>
              </el-table>
          </el-tab-pane>

          </el-tabs>
        </el-main>
      </el-container>
    </el-col>
  </el-row>
</section>
</template>
<script>
export default {
  data() {
    return {
      TbHeaderStyle:{
        'word-wrap':'break-word',
        'color':'black'
      },
      precise:true,
      activeName: "first",
      keyword: "",
      keyword1: "",
      country: "美国",
      tableData:[],
      loading: false,
      tableData1:[],
      loading1: false
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    BeautySearch(){
     let self = this
     this.loading = true
     var reg = new RegExp(/^[0-9A-Z]+$/)
     if(reg.test(this.keyword)){
       var mode = '/selectAsin/'
     } else{
       var mode = '/selectTi/'
     }  
     this.axios({
       'path':'get',
       'url':mode+this.keyword
     },{
       timeout:1000*60*10
     }).then(function(res){
        var a = res.data.replace(/\'/g,"\"")
        var b = a.replace(/,\s/g,",")
        var c = b.replace(/L\,/g,",") 
        var d = c.replace(/L\}/g,"}").replace(/,u\"/g,",\"").replace(/{u\"/g,"{\"")
        var e = d.match(/datetime\.date\(([^A-Za-z]+)\)/g)
        console.log(d)
        for(var i in e){
          var f = e[i].match(/\((\S*)\)/g)
          var g = f[0].replace(/\(/,"").replace(/\)/,"").split(",")
          var date = '"'+g[0]+'-'+g[1]+'-'+g[2]+'"'
          var h = d.replace(/datetime\.date\(([^A-Za-z]+)\)/g,date)
        }
        self.tableData = JSON.parse(h)
        self.loading = false
     }).catch(function (error) {
          self.tableData=[]
          self.loading = false
          console.log(error)
          self.$message({
            showClose: true,
            message: '未能查询到任何数据，请检查您输入的信息！',
            type: 'error'
          })
        })
    },
    KeywdSearch(){
      let self = this
      this.loading1 = true
      if(this.precise){
        var mode = '/selectkeyword/'
      } else{
        var mode = '/selectkeywordlike/'
      }
      this.axios({
      'path':'get',
       'url':mode+this.keyword1
      },{
       timeout:1000*60*10
     }).then(function(res){
        var a1 = res.data.replace(/\'/g,"\"").replace(/,\s/g,",").replace(/L\,/g,",").replace(/L\}/g,"}").replace(/{u\"/g,"{\"")
        self.tableData1 = JSON.parse(a1)
        self.loading1 = false
        console.log(self.tableData1)
        console.log(res.data)
      }).catch(function (error) {
          self.tableData1=[]
          self.loading1 = false
          console.log(error)
          self.$message({
            showClose: true,
            message: '未能查询到任何数据，请检查您输入的信息！',
            type: 'error'
          });
        })
    }
  }
};
</script>


