<template>
<section>
    <!-- 搜索框 -->
    <el-row style="margin-top:50px;">
        <el-col :span="10">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="站点">
                    <el-select v-model="form.country" placeholder="美国">
                    <el-option label="美国" value="1"></el-option>
                    <el-option label="英国" value="2"></el-option>
                    <el-option label="日本" value="3"></el-option>
                    <el-option label="加拿大" value="4"></el-option>
                    <el-option label="德国" value="5"></el-option>
                    <el-option label="法国" value="6"></el-option>
                    <el-option label="西班牙" value="7"></el-option>
                    <el-option label="意大利" value="8"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="ASIN">
                    <el-input v-model="form.asin" placeholder="请输入ASIN"></el-input>
                </el-form-item>
                <el-form-item label="关键词">
                    <el-input v-model="form.keyWords" placeholder="请输入关键词"></el-input>
                </el-form-item>
            </el-form>
        </el-col>
        
    </el-row>

    <!-- 结果表格 -->
    <el-row style="margin-top:50px;">
        <el-col :span="8">
            <el-table
            :data="tableData"
            :header-cell-style="TbHeaderStyle"
            border>
            <el-table-column
                prop="asin"
                label="ASIN"
                width="180">
            </el-table-column>
            <el-table-column
                prop="keyword"
                label="关键词"
                width="180">
            </el-table-column>
            <el-table-column
                header-align="center"
                align="center"
                :class-name="impt"
                prop="rank"
                label="排名">
            </el-table-column>
            </el-table>
        </el-col>
    </el-row>

</section>
</template>
<script>
export default {
  data () {
    return {
        important:'impt',
        TbHeaderStyle:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },
        form:{
            country:'美国',
            keyWords:'',
            asin:''
        },
        tableData: [{
            asin: 'B000WWK844',
            keyword: 'Jacquard',
            rank: '100+'
          }]
    }
  }
}
</script>
<style lang="css" scoped>
 .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
.impt .cell{
    color: red !important;
}
</style>


