import Vue from 'vue'
import Router from 'vue-router'
import AssistantSelection from '@/views/AssistantSelection'
import Hotorder from '@/views/AssistantSelection/Hotorder'
import Asinorder from '@/views/AssistantSelection/Asinorder'
import Keywordtools from '@/views/Keywordtools'
import Monitor from '@/views/Monitor'
import Subscribe from '@/views/Subscribe'
import Superurl from '@/views/Superurl'
import Proanalysis from '@/views/Proanalysis'
import Followsale from '@/views/Followsale'
import FollowManage from '@/views/Followsale/Manage'
import FollowSet from '@/views/Followsale/Set'
import FollowTemplate from '@/views/Followsale/Template'
import FollowParameter from '@/views/Followsale/Parameter'
import Followdetails from '@/views/Followsale/Followdetails'
import ReviewMonitor from '@/views/ReviewMonitor'
import ReviewDetail from '@/views/ReviewMonitor/ReviewDetail'
import KeywordMonitor from '@/views/KeywordMonitor'
import Login from '@/views/Login'
import Layout from '@/Layout'

Vue.use(Router)


const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Layout,
      children: [
    {
      path: '/AssistantSelection',
      name: 'AssistantSelection',
      component: AssistantSelection,
      children: [
        { path: '/AssistantSelection/hot', component: Hotorder },
        { path: '/AssistantSelection/asin', component: Asinorder }
      ]
    },
    {
      path: '/keywordtools',
      name: 'Keywordtools',
      component: Keywordtools
    },
    {
      path: '/monitor',
      name: 'Monitor',
      component: Monitor
    },
    {
      path: '/subscribe',
      name: 'Subscribe',
      component: Subscribe
    },
    {
      path: '/superurl',
      name: 'Superurl',
      component: Superurl
    },
    {
      path: '/productanalysis',
      name: 'Proanalysis',
      component: Proanalysis
    },
    {
      path: '/followsale',
      name: 'Followsale',
      component: Followsale,
      children: [
        { path: '/followsale/manage', component: FollowManage },
        { path: '/followsale/set', component: FollowSet },
        { path: '/followsale/template', component: FollowTemplate},
        { path: '/followsale/parameter', component: FollowParameter },
        { path: '/followsale/details/:asin', component: Followdetails }
      ]
    },
    {
      path: '/reviewmonitor',
      name: 'ReviewMonitor',
      component: ReviewMonitor,
    },
    { path: '/reviewmonitor/details/:asin', component: ReviewDetail },
    {
      path: '/keywordmonitor',
      name: 'KeywordMonitor',
      component: KeywordMonitor,
    }
  ]},
    {
      path: '/login',
      name: 'Login',
      component: Login,
    } 
  ]
})
router.beforeEach(function(to, from, next){
  next()
})

export default router
  
  



