
<template>
<el-row>
    <el-col :span="20" :offset="2">
        <el-container>
            <el-header>
                Super URL
            </el-header>
            <el-main>
                <el-row>
                    <el-col :span="8" class="condition">
                        <el-form ref="form" :model="form" label-width="82px" >
                            <el-form-item label="站点：">
                                <el-select v-model="form.country" placeholder="美国">
                                <el-option label="美国" value="1"></el-option>
                                <el-option label="英国" value="2"></el-option>
                                <el-option label="日本" value="3"></el-option>
                                <el-option label="加拿大" value="4"></el-option>
                                <el-option label="德国" value="5"></el-option>
                                <el-option label="法国" value="6"></el-option>
                                <el-option label="西班牙" value="7"></el-option>
                                <el-option label="意大利" value="8"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="Seller ID：">
                                <el-input placeholder="请输入Seller ID"></el-input>
                            </el-form-item>
                            <el-form-item label="ASIN：">
                                <el-input placeholder="请输入ASIN"></el-input>
                            </el-form-item>
                            <el-form-item label="关键词：">
                                <el-input
                                type="textarea"
                                :rows="3"
                                autosize
                                placeholder="请输入关键词，多个关键词请分行">
                                </el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-col :offset="6">
                                <el-button type="primary">生成</el-button>
                                <el-button>清空</el-button>
                                </el-col>
                            </el-form-item>
                            
                        </el-form>
                    </el-col>
                    <el-col :span="14" :offset="1">
                        <el-table
                            border
                            :header-cell-style="header"
                            :data="tableData"
                            style="width: 100%;"
                            stripe>
                            <el-table-column
                            prop="country"
                            label="国家">
                            </el-table-column>
                            <el-table-column
                            prop="asin"
                            label="ASIN">
                            </el-table-column>
                            <el-table-column
                            prop="kywdsNum"
                            label="关键词数">
                            </el-table-column>
                            <el-table-column
                            prop="seller"
                            label="Seller ID">
                            </el-table-column>
                            <el-table-column
                            prop="click"
                            label="点击数">
                            </el-table-column>
                            <el-table-column
                            prop="supurl"
                            label="Super URL">
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
        header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },
        tableData: [{
          country:'美国',
          asin: 'B001A4XDBS',
          kywdsNum: '2',
          seller:'ATRUNAO4OVJL5',
          click:'0',
          supurl:'www.baidu.com'
        },{
          country:'美国',
          asin: 'B001A4XDBS',
          kywdsNum: '2',
          seller:'ATRUNAO4OVJL5',
          click:'0',
          supurl:'www.baidu.com'
        }],
        form: {
          country:'美国'
        }
    }
  },
  mounted(){
  },
  methods: {
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      }
      }
}
</script>
<style lang="css" scoped>
.el-header{
    background-color: #fff;
    border-bottom: 1px solid #ccc; 
    line-height: 60px;
}
.condition{
    background-color: whitesmoke;
    padding: 20px;
}
</style>


