<style lang="css" scoped>
.logo{
    width: 100%;
    height: 100%;
    text-align: center;
}
.logo span{
    font-size:30px;
    font-weight: bold;
    color: #fff;
    line-height: 60px;
}
.head{
  border-radius: 50%;
  width: 40px;
  height: 40px;
  float: left;
}
.user{
  line-height: 60px;
}
.el-badge__content{
  border: none;
  margin:3px;
  font-weight: bold;
}
</style>
<template>
    <el-container>
    <el-header>
        <el-row>
            <el-col :span="3">
                <div class="logo">
                    <span>LOGO</span>
                </div>
            </el-col>
            <el-col :span="19">
                <el-menu
                :default-active="activeIndex"
                class="el-menu-demo"
                mode="horizontal"
                @select="handleSelect"
                background-color="#545c64"
                text-color="#fff"
                active-text-color="#ffd04b">
                    <router-link to="/AssistantSelection/hot"><el-menu-item index="1">选品助手</el-menu-item></router-link>
                    <router-link to="/Keywordtools"><el-menu-item index="2">关键词工具</el-menu-item></router-link>
                    <router-link to="/monitor"><el-menu-item index="3">我的监控</el-menu-item></router-link>
                    <router-link to="/subscribe"><el-menu-item index="4">我的订阅</el-menu-item></router-link>
                    <el-submenu index="5">
                        <template slot="title">功能大全</template>
                        <router-link to="/superurl"><el-menu-item class="fa fa-link" index="2-1"> Super URL</el-menu-item></router-link>
                        <router-link to="/productanalysis"><el-menu-item class="fa fa-bar-chart" index="2-2"> 产品分析</el-menu-item></router-link>
                        <router-link to="/followsale/manage"><el-menu-item class="fa fa-bullhorn" index="2-3"> 跟卖管理</el-menu-item></router-link>
                        <router-link to="/reviewmonitor"><el-menu-item class="fa fa-eye" index="2-4"> 差评监控</el-menu-item></router-link>
                        <router-link to="/keywordmonitor"><el-menu-item class="fa fa-crosshairs" index="2-4"> 关键词监控</el-menu-item></router-link>
                        <el-menu-item class="fa fa-search" index="2-5"> 商标查询</el-menu-item>
                        <el-menu-item class="fa fa-calculator" index="2-6"> 成本利润</el-menu-item>
                    </el-submenu>
                 </el-menu>
            </el-col>
            <el-col :span="2">
              <div class="user">
                
                <el-badge :value="3" class="item">
                  <img src="./assets/head.jpg" class="head" alt="">
                  <!-- <el-button size="small" icon="fa fa-bell bell"></el-button> -->
                </el-badge>
              </div>
            </el-col>
        </el-row>
    </el-header>
    <el-main>
      <router-view></router-view>
    </el-main>
</el-container>
</template>
<script>
export default {
  data () {
    return {
        activeIndex: "",
        isCollapse: "true"
    }
  },
    methods: {
        handleSelect(key, keyPath) {
        console.log(key, keyPath);
        }
    }
}
</script>


