<style lang="css" scoped>
.el-select{
    width:120px;
}
</style>
<template>
<el-row>
    <el-col :span="22" :offset="1">
        <el-row>
            <el-col :span="8" :offset="1">
                <el-input placeholder="请输入您要查询的ASIN" size="small" v-model="asin" class="input-with-select">
                    <el-select v-model="country" slot="prepend" placeholder="请选择">
                    <el-option label="美国" value="1"></el-option>
                    <el-option label="英国" value="2"></el-option>
                    <el-option label="日本" value="3"></el-option>
                    <el-option label="加拿大" value="4"></el-option>
                    <el-option label="德国" value="5"></el-option>
                    <el-option label="法国" value="6"></el-option>
                    <el-option label="西班牙" value="7"></el-option>
                    <el-option label="意大利" value="8"></el-option>
                    </el-select>
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4" :offset="11">
                <el-button type="primary" size="small">添加ASIN</el-button>
                <el-dropdown size="small" @command="handleCommand">
                    <el-button size="small" type="primary">
                        批量操作<i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="a">批量绑定店铺</el-dropdown-item>
                    <el-dropdown-item command="b">排除卖家</el-dropdown-item>
                    <el-dropdown-item command="c">启用</el-dropdown-item>
                    <el-dropdown-item command="d">删除</el-dropdown-item>
                </el-dropdown-menu>
                </el-dropdown>
            </el-col>
        </el-row>
        <el-row style="margin-top:50px;">
            <el-col :span="24">
                <el-table
                :header-cell-style="header"
                :data="tableData"
                border
                style="width: 100%">
                    <el-table-column
                    align="center"
                    type="selection">
                        
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="产品图片">
                        <template slot-scope="scope">
                                <div class="rs-img">
                                <img src="../../assets/test.jpg" alt="">
                                </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="asin"
                        align="center"
                        label="ASIN">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="店铺账号">
                        <template slot-scope="scope">
                            <span>{{ scope.row.account === ''?'绑定店铺':scope.row.account }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="sellers"
                        align="center"
                        label="出现卖家数"
                        width="120">
                    </el-table-column>
                    <el-table-column
                        prop="eliminate"
                        align="center"
                        label="排除卖家数"
                        width="120">
                    </el-table-column>
                    <el-table-column
                        prop="state"
                        align="center"
                        label="状态"
                        width="100">
                    </el-table-column>
                   <el-table-column
                        prop="date"
                        align="center"
                        sortable
                        label="最新监控时间">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="操作">
                        <template slot-scope="scope">
                            <router-link  :to="'/followsale/details/'+scope.row.asin"><el-button size="mini" type="primary"  icon="fa fa-search fa-lg"></el-button></router-link>
                            <el-button style="margin-left:10px;" size="mini" type="danger" icon="fa fa-trash-o fa-lg"></el-button>
                            <el-button size="mini" @click="scope.row.dialogFormVisible = true" type="info" icon="fa fa fa-filter fa-lg"></el-button>
                            <el-dialog title="排除卖家" :visible.sync="scope.row.dialogFormVisible">
                             <el-form :model="scope.row.ExcludedSeller">
                                <el-form-item>
                                    <el-input v-model="scope.row.ExcludedSeller.seller" auto-complete="off"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="scope.row.dialogFormVisible = false">取 消</el-button>
                                <el-button type="primary" @click="scope.row.dialogFormVisible = false">确 定</el-button>
                            </div>
                            </el-dialog>
                        </template>
                    </el-table-column>
                </el-table>
                
            </el-col>
        </el-row>
    </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
        header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },  
        country:'美国',
        asin:'',
        tableData: [{ 
            img: 'test.jpg',
            asin: '0062110721',
            account:'',
            sellers:'24',
            eliminate:'1',
            state:'待监控',
            date:'2017-12-14 07:38:30',
            dialogFormVisible: false,
            ExcludedSeller:{
                seller:''
            }
          },
          { 
            img: 'test.jpg',
            asin: '0062110721',
            account:'924081587',
            sellers:'24',
            eliminate:'1',
            state:'待监控',
            date:'2017-12-14 07:38:30',
            dialogFormVisible: false,
            ExcludedSeller:{
                seller:''
            }
          }]
          }
    },
  methods:{
      handleCommand(command){
          console.log(command)
      },
      aaa(ds){
         ds = true;
      }

  }
}
</script>


