<style lang="css" scoped>
.el-header{
    background-color: #fff;
    border-bottom: 1px solid #ccc; 
    line-height: 60px;
}
.el-select{
  width:120px;
}
.rs-img{
    width: 100px;
    margin-right: 10px;
}
.rs-img img{
    width: 100%;
}
.keywords{
    cursor: pointer;
    color: #409EFF;
}
</style>
<template>
<el-row>
    <el-col :span="22" :offset="1">
        <el-container>
            <el-header>
                产品监控
            </el-header>
            <el-main>
                <el-row>
                <el-col :span="8" :offset="8" style="margin-top:30px">
                    <el-input placeholder="请输入您要查询的ASIN" v-model="keyword" class="input-with-select">
                        <el-select v-model="country" slot="prepend" placeholder="请选择">
                        <el-option label="美国" value="1"></el-option>
                        <el-option label="英国" value="2"></el-option>
                        <el-option label="日本" value="3"></el-option>
                        <el-option label="加拿大" value="4"></el-option>
                        <el-option label="德国" value="5"></el-option>
                        <el-option label="法国" value="6"></el-option>
                        <el-option label="西班牙" value="7"></el-option>
                        <el-option label="意大利" value="8"></el-option>
                        </el-select>
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                </el-row>
                <el-row style="margin-top:50px;">
                    <el-col :span="3" :offset="21">
                        <el-button type="primary" @click="dialogFormVisible = true" size="small">添加产品</el-button>
                        <el-dialog title="添加ASIN" :visible.sync="dialogFormVisible">
                        <el-form :model="form">
                            <el-form-item label="ASIN" v-model="form.asins" :label-width="120">
                            <el-input
                            type="textarea"
                            v-model="form.name"
                            auto-complete="off"
                            :autosize="{ minRows: 4}"
                            placeholder="多个ASIN请换行输入,一行一个">
                            </el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible = false">取 消</el-button>
                            <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                        </div>
                        </el-dialog>
                        <el-button type="primary" size="small">批量删除</el-button>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24" :offset="0" style="margin-top:10px;">
                        <el-table
                        :header-cell-style="header"
                        :data="tableData"
                        border
                        style="width: 100%">
                            <el-table-column
                            type="selection">
                                
                            </el-table-column>
                            <el-table-column
                                label="产品信息"
                                width="300">
                                <template slot-scope="scope">
                                    <div style="overflow:hidden;display: flex;align-items: center;">
                                        <div class="rs-img">
                                        <img src="../assets/test.jpg" alt="">
                                    </div>
                                    <div style="width:160px;">
                                        <span>{{ scope.row.name }}</span>
                                        <br>
                                        <span style="color:#909399;"><strong>ASIN: </strong>{{ scope.row.asin }}</span>
                                    </div>
                                    </div>
                                </template>
                            </el-table-column>

                            <el-table-column
                                prop="price"
                                sortable
                                label="现价"
                                align="center"
                                width="100"
                                >
                            </el-table-column>

                            <el-table-column
                                prop="rank"
                                sortable
                                align="center"
                                width="100"
                                label="Rank">
                            </el-table-column>

                            <el-table-column
                                prop="comments"
                                sortable
                                align="center"
                                label="评论总数"
                                >
                            </el-table-column>
                            
                            <el-table-column
                                prop="sellers"
                                sortable
                                align="center"  
                                label="卖家数">
                            </el-table-column>

                            <el-table-column
                                align="center"
                                label="预估销售量（额)／天">
                                <template slot-scope="scope">
                                    <span>{{ scope.row.sales.salesVolume }} (${{ scope.row.sales.salesAmount }})</span>
                                </template>
                            </el-table-column>

                            <el-table-column
                                align="center"
                                label="关键词数">
                                <template slot-scope="scope">
                                    <el-button type="text">{{ scope.row.keywords>0 ? scope.row.keywords:'添加关键词' }}</el-button>
                                </template>
                            </el-table-column>

                            <el-table-column
                                prop="address"
                                align="center"
                                label="操作">
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </el-col>
</el-row>

</template>
<script>
export default {
  data () {
    return {
        form:{
            asins:[]
        },
        dialogTableVisible: false,
        dialogFormVisible: false,
        header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },  
        country:'美国',
        keyword:'',
        tableData: [{ 
            img: 'test.jpg',
            name: 'Jacquard Products 1 Ounce Arabic Gum',
            asin: '0062110721',
            price:'4.03',
            brand:'N.A.',
            rank:'5951',
            comments:'72',
            category:'Sports & Outdoors',
            sales:{
                salesAmount:'28.21',
                salesVolume:'7'
            },
            stars:'4.7',
            sellers:'24',
            keywords:'10'
          },
          { 
            img: 'test.jpg',
            name: 'Jacquard Products 1 Ounce Arabic Gum',
            asin: '0062110721',
            price:'4.03',
            brand:'N.A.',
            rank:'5951',
            comments:'72',
            category:'Sports & Outdoors',
            sales:{
                salesAmount:'38.21',
                salesVolume:'10'
            },
            stars:'4.7',
            sellers:'24',
            keywords:''
          }]
    }
  },
  methods:{}
}
</script>


