<style lang="css" scoped>
.el-header{
    background-color: #fff;
    border-bottom: 1px solid #ccc; 
    line-height: 60px;
}
</style>
<template>
 <el-row>
        <el-col :span="18" :offset="3">
<el-container>
    <el-header>
        订阅列表
    </el-header>
    <el-main>
        <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="类目上升产品" name="first">
        <el-row>
            <el-col :span="9">
                <el-date-picker
                v-model="data"
                align="right"
                type="date"
                placeholder="选择日期" 
                size="small"
                :picker-options="pickerOptions1">
                </el-date-picker>
                <el-select v-model="read" placeholder="请选择" size="small">
                    <el-option label="全部" value="1"></el-option>
                    <el-option label="未读" value="2"></el-option>
                    <el-option label="已读" value="3"></el-option>
                </el-select>
            </el-col>
            <el-col :span="4" :offset="11">
                <el-button size="small">批量设为已读</el-button>
                <el-button type="primary" icon="fa fa-gear" size="small"></el-button>
            </el-col>
        </el-row>
    </el-tab-pane>
    <el-tab-pane label="关键词上升产品" name="second"><el-row>
            <el-col :span="9">
                <el-date-picker
                v-model="data"
                align="right"
                type="date"
                placeholder="选择日期" 
                size="small"
                :picker-options="pickerOptions1">
                </el-date-picker>
                <el-select v-model="read" placeholder="请选择" size="small">
                    <el-option label="全部" value="1"></el-option>
                    <el-option label="未读" value="2"></el-option>
                    <el-option label="已读" value="3"></el-option>
                </el-select>
            </el-col>
            <el-col :span="4" :offset="11">
                <el-button size="small">批量设为已读</el-button>
                <el-button type="primary" icon="fa fa-gear" size="small"></el-button>
            </el-col>
        </el-row>
        </el-tab-pane>
    <el-tab-pane label="每日新词" name="third">
        <el-row>
        <el-col :span="9">
            <el-date-picker
            v-model="data"
            align="right"
            type="date"
            placeholder="选择日期" 
            size="small"
            :picker-options="pickerOptions1">
            </el-date-picker>
            <el-select v-model="read" placeholder="请选择" size="small">
                <el-option label="全部" value="1"></el-option>
                <el-option label="未读" value="2"></el-option>
                <el-option label="已读" value="3"></el-option>
            </el-select>
        </el-col>
        <el-col :span="4" :offset="11">
            <el-button size="small">批量设为已读</el-button>
            <el-button type="primary" icon="fa fa-gear" size="small"></el-button>
        </el-col>
        </el-row>
    </el-tab-pane>
    <el-tab-pane label="关键词新卖家" name="fourth">
         <el-row>
        <el-col :span="9">
            <el-date-picker
            v-model="data"
            align="right"
            type="date"
            placeholder="选择日期" 
            size="small"
            :picker-options="pickerOptions1">
            </el-date-picker>
            <el-select v-model="read" placeholder="请选择" size="small">
                <el-option label="全部" value="1"></el-option>
                <el-option label="未读" value="2"></el-option>
                <el-option label="已读" value="3"></el-option>
            </el-select>
        </el-col>
        <el-col :span="4" :offset="11">
            <el-button size="small">批量设为已读</el-button>
            <el-button type="primary" icon="fa fa-gear" size="small"></el-button>
        </el-col>
        </el-row>

    </el-tab-pane>
  </el-tabs>
    </el-main>
</el-container>
</el-col>
</el-row>
</template>
<script>
export default {
    data() {
      return {
        activeName: 'first',
        data:'',
        read:'',
        pickerOptions1: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        }
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      }
    }
  };
</script>


