<style lang="css" scoped>
</style>
<template>
<el-row>
  <el-col :span="22" :offset="1">
    <el-row>
      <el-col :span="6">
        <el-input size="small" placeholder="请输入email">
          <el-button slot="append" size="small">添加</el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-row style="margin-top:30px;">
      <el-col :span="9">
        <el-table
      :data="tableData">
      <el-table-column
        prop="email"
        label="跟卖邮箱提醒">
      </el-table-column>
      <el-table-column
        align="center"
        label="操作">
        <template slot-scope="scope">
          <el-button type="primary" icon="fa fa-edit" ></el-button>
          <el-button type="danger" icon="fa fa-trash-o"></el-button>
        </template>
      </el-table-column>
    </el-table>
      </el-col>
    </el-row>
  </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
      tableData: [{
            email: '924081587@qq.com'
          }]
    }
  }
}
</script>


