<style lang="css" scoped>
.el-header{
    background-color: #fff;
    border-bottom: 1px solid #ccc; 
    line-height: 60px;
}
.rs-img{
    width: 100px;
    float: left;
    margin-right: 10px;
}
.rs-img img{
    width: 100%;
}
.badkywd{
  color: #409EFF;
  font-weight: bold;
  font-size:18px;
}
.review{
  font-size:12px;
}
.asin{
  font-size:12px;
  color: #409EFF;
}
.tools{
  margin-top:30px;
  font-size:12px;
}
.tools span{
  margin-right: 10px;
}
.tools span i{
  color: red;
  font-weight: bold;
}
</style>
<template>
<el-row>
  <el-col :span="22" :offset="1">
    <el-container>
      <el-header>
        差评监控
      </el-header>
      <el-main>
        <el-row>
      <el-col :span="6" :offset="9">
        <el-input size="small" placeholder="请输入ASIN">
          <el-button slot="append" size="small">查询</el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-row class="tools" type="flex" style="align-items:center;">
      <el-col :span="6">
        <span>差评总数：<i>1</i></span> <span>ASIN总数：<i>1</i></span> <span>开启监控数量：<i>1</i></span>
      </el-col>
      <el-col :span="3" :offset="15">
        <el-button type="primary" @click="dialogFormVisible = true" size="small">添加产品</el-button>
        <el-button type="primary" size="small">批量启用</el-button>
        <el-dialog title="添加ASIN" :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="ASIN" :label-width="120">
            <el-input
            type="textarea"
            v-model="form.asin"
            auto-complete="off"
            :autosize="{ minRows: 4}"
            placeholder="多个ASIN请换行输入,一行一个">
            </el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
        </el-dialog>
      </el-col>
    </el-row>
    <el-row style="margin-top:10px;">
      <el-col :span="24">
        <el-table
        border
        :header-cell-style="header"
      :data="tableData">
      <el-table-column
      type="selection"
      align="center">
      </el-table-column>
      <el-table-column
      label="产品信息"
      width="800"
      header-align="center">
      <template slot-scope="scope">
           <div style="overflow:hidden;display: flex;align-items: center;">
              <div class="rs-img">
                  <img src="../assets/test.jpg" alt="">
              </div>
              <div style="width:650px;">
                <span class="badkywd">{{ scope.row.badkywd }}</span>
                <br>
                <span class="review">{{ scope.row.review }}</span>
                <br>
                <span class="asin">{{ scope.row.asin }}</span>
              </div>
          </div>
      </template>
      </el-table-column>
      <el-table-column
      label="星级"
      align="center">
      <template slot-scope="scope">
        <el-rate
          v-model="scope.row.stars"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value}">
        </el-rate>
      </template>
      </el-table-column>
      <el-table-column
      label="开始监控日期"
      prop="startDate"
      align="center">
      </el-table-column>
      <el-table-column
      label="评论日期"
      prop="reviewDate"
      align="center">
      </el-table-column>
      <el-table-column
        align="center"
        label="操作"
        width="200">
        <template slot-scope="scope">
          <router-link :to="'reviewmonitor/details/' + scope.row.asin"><el-button size="mini" type="primary"  icon="fa fa-search fa-lg"></el-button></router-link>
          <el-button size="mini" type="danger" icon="fa fa-trash-o fa-lg"></el-button>
          <el-button size="mini" type="info" icon="fa fa-mail-forward"></el-button>
        </template>
      </el-table-column>
    </el-table>
      </el-col>
    </el-row>
      </el-main>
    </el-container>
    
  </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
      dialogFormVisible:false,
      header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },
        form:{
          asin:''
        },
      tableData:[
        {
          img:'',
          badkywd:"Wouldn't buy again",
          review:"Super poor quality. Tried making ink from tea and it just clumped and wouldn't thicken my ink. Used 3/4 oz when recipe said use 1 tsp and it still didn't work.",
          asin:'B000WWK844',
          stars:2,
          startDate:'2017-12-13',
          reviewDate:'2017-10-01'
        }
      ]
    }
  }
}
</script>


