<style lang="css" scoped>
.el-select{
    width:120px;
}
</style>
<template>
<el-row>
    <el-col :span="22" :offset="1">
        <el-row>
            <el-col :span="8" :offset="1">
                <el-input placeholder="请输入您要查询的模板名称" size="small" v-model="asin" class="input-with-select">
                    <el-select v-model="country" slot="prepend" placeholder="请选择">
                    <el-option label="美国" value="1"></el-option>
                    <el-option label="英国" value="2"></el-option>
                    <el-option label="日本" value="3"></el-option>
                    <el-option label="加拿大" value="4"></el-option>
                    <el-option label="德国" value="5"></el-option>
                    <el-option label="法国" value="6"></el-option>
                    <el-option label="西班牙" value="7"></el-option>
                    <el-option label="意大利" value="8"></el-option>
                    </el-select>
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4" :offset="11">
                <el-button type="primary" size="small">添加模板</el-button>
                <el-button type="primary" size="small">批量删除</el-button>
            </el-col>
        </el-row>
        <el-row style="margin-top:50px;">
            <el-col :span="24">
                <el-table
                :header-cell-style="header"
                :data="tableData"
                border
                style="width: 100%">
                    <el-table-column
                    type="selection">
                        
                    </el-table-column>
                      <el-table-column
                      prop="country"
                      align="center"
                      label="应用站点">
                        
                      </el-table-column>
                      <el-table-column
                      prop="name"
                      align="center"
                      label="模板名称">

                      </el-table-column>
                   <el-table-column
                        prop="date"
                        align="center"
                        label="添加时间">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="操作">
                        <template slot-scope="scope">
                          <el-button type="primary" size="mini" icon="fa fa-edit fa-lg"></el-button>
                          <el-button type="danger" size="mini" icon="fa fa-trash-o fa-lg"></el-button>
                          <el-button size="mini" type="info">设为默认</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
        header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },  
        country:'美国',
        asin:'',
        tableData: [{ 
            img: 'test.jpg',
            country:'美国',
            name:"Template",
            date:'2017-12-14'
          }]
          }
    },
  methods:{
      handleCommand(command){
          console.log(command)
      }
  }
}
</script>


