<style lang="css" scoped>
</style>
<template>
<el-row>
  <el-col :span="22" :offset="1">
    <el-row>
      <el-col :span="6">
        <el-input size="small" placeholder="请输入店铺名称/卖家编号">
          <el-button slot="append" size="small">添加</el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-row style="margin-top:30px;">
      <el-col :span="24">
        <el-table
        border
        :header-cell-style="header"
      :data="tableData">
      <el-table-column
      prop="shopname"
      align="center"
        label="店铺名称">
      </el-table-column>
        <el-table-column
        prop="sellerId"
        label="卖家编号">
      </el-table-column>
      <el-table-column
      prop="FBA"
      align="center"
        label="FBA">
      </el-table-column>
      <el-table-column
        prop="ordinary"
        align="center"
        label="普通卖家">
      </el-table-column>
      <el-table-column
        prop="selfSupport"
        align="center"
        label="亚马逊自营">
      </el-table-column>
      <el-table-column
      prop="price"
      align="center"
        label="价格（含运费）">
      </el-table-column>
      <el-table-column
      prop="date"
        label="监控时间">
      </el-table-column>
      <el-table-column
        align="center"
        label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="fa fa-mail-forward"></el-button>
        </template>
      </el-table-column>
    </el-table>
      </el-col>
    </el-row>
  </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
         header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },
        tableData: [{
            shopname:'THE BT GROUP',
            sellerId:'A2FQ5GG01HBOZ1',
            FBA:'',
            ordinary:'是',
            selfSupport:'否',
            price:'4.8',
            date:'2017-12-14 07:38:30'
            }]
    }
  }
}
</script>


