<style lang="css" scoped>
.btline{
    padding-bottom: 10px;
    border-bottom: 1px solid #DFE4ED;
}
.myorder{
    color: #878D99;
    font-size: 14px;
}
.orderby{
    margin-top: 20px;
}
.line{
    text-align: center;
}
.more{
    color: #409EFF;
    font-size: 14px;
    float: right;
    margin-right:30px;
    cursor: pointer;
}
.el-form{
    font-size: 14px;
}
.right{
    float: right;
}
.rs-img{
    width: 100px;
    float: left;
    margin-right: 10px;
}
.rs-img img{
    width: 100%;
}
.fade-leave-to{
    height:0px;
    overflow: hidden;
}
.fade-enter-to{
    height:auto;
}
.fade-enter-active {
  transition: height 3s ease;
}
.fade-leave-active {
  transition: all 3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
</style>
<template>
<section>
    <el-row class="btline">
        <el-col :span="24">
            <span class="myorder">我的查询：</span>
            <el-tag type="info" closable>历史记录</el-tag>
            <el-tag type="info" closable>历史记录</el-tag>
            <span class="more" @click="show()">{{ showbutton.name }} <i :class="['fa',showbutton.name==='更多'?'fa-angle-down':'fa-angle-up']"></i></span>
        </el-col>
    </el-row>

    <!-- 搜索条件 -->
    <el-row>
        <el-col :span="24" class="orderby btline" >
            <el-form ref="form" :inline="true" :model="form" class="demo-form-inline" label-width="100px" size='small'>
            <el-form-item label="站点">
                <el-select v-model="form.country" placeholder="美国">
                <el-option label="美国" value="1"></el-option>
                <el-option label="英国" value="2"></el-option>
                <el-option label="日本" value="3"></el-option>
                <el-option label="加拿大" value="4"></el-option>
                <el-option label="德国" value="5"></el-option>
                <el-option label="法国" value="6"></el-option>
                <el-option label="西班牙" value="7"></el-option>
                <el-option label="意大利" value="8"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="查询日期">
                <el-date-picker
                v-model="form.date"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="-"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="Rank">
                <el-input v-model="form.minRank" style="width: 200px;" placeholder="最小值"></el-input>
                -
                <el-input v-model="form.maxRank" style="width: 200px;" placeholder="最大值"></el-input>
            </el-form-item>
            <el-form-item label="类目">
                <el-cascader :options="options" v-model="selectedOptions">
                </el-cascader>
            </el-form-item>
            <el-form-item label="Reviews数">
                <el-input v-model="form.minReviews" style="width: 200px;" placeholder="最小值"></el-input>
                -
                <el-input v-model="form.maxReviews" style="width: 200px;" placeholder="最大值"></el-input>
            </el-form-item>
            <el-form-item label="卖家类型">
                <el-radio-group v-model="form.sellerType" size="small">
                    <el-radio label="1" border>FBA</el-radio>
                    <el-radio label="2" border>FBM</el-radio>
                </el-radio-group>
            </el-form-item>
            <transition name="fade">
                <el-collapse-transition>
            <div class="hide" v-if="isShow">
                <el-form-item label="Asin">
                    <el-input v-model="form.asin" placeholder="请输入Asin"></el-input>
                </el-form-item>
                <el-form-item label="预估销量">
                    <el-input v-model="form.minSalesVolume" style="width: 200px;" placeholder="最小值"></el-input>
                    -
                    <el-input v-model="form.maxSalesVolume" style="width: 200px;" placeholder="最大值"></el-input>
                </el-form-item>
                <el-form-item label="预估销售额">
                    <el-input v-model="form.minSalesAmount" style="width: 200px;" placeholder="最小值"></el-input>
                    -
                    <el-input  v-model="form.maxSalesAmount" style="width: 200px;" placeholder="最大值"></el-input>
                </el-form-item>
                <el-form-item label="卖家">
                    <el-input v-model="form.sellerId" placeholder="请输入卖家名称"></el-input>
                </el-form-item>
                 <el-form-item label="价格">
                    <el-input v-model="form.minPrice" style="width: 200px;" placeholder="最小值"></el-input>
                    -
                    <el-input v-model="form.maxPrice" style="width: 200px;" placeholder="最大值"></el-input>
                </el-form-item>
                <el-form-item label="Title(不包含)">
                    <el-input v-model="form.untitle" placeholder="Title(不包含)"></el-input>
                </el-form-item>
                 <el-form-item label="Title(包含)">
                    <el-input v-model="form.title" placeholder="Title(不包含)"></el-input>
                </el-form-item>
                 <el-form-item label="品牌">
                    <el-input v-model="form.brand" placeholder="请输入品牌"></el-input>
                </el-form-item>
                <el-form-item label="跟卖数">
                    <el-input v-model="form.minFollowSell" style="width: 200px;" placeholder="最小值"></el-input>
                    -
                    <el-input v-model="form.maxFollowSell" style="width: 200px;" placeholder="最大值"></el-input>
                </el-form-item>
                <el-form-item label="Review星级">
                    <el-input v-model="form.minStarLevel" style="width: 200px;" placeholder="最小值"></el-input>
                    -
                    <el-input v-model="form.maxStarLevel" style="width: 200px;" placeholder="最大值"></el-input>
                </el-form-item>
            </div>
            </el-collapse-transition>
            </transition>
             <el-row type="flex" justify="end">
                    <el-col :span="2"><el-button  type="primary" @click="onSubmit">查询</el-button></el-col>
                    <el-col :span="2"><el-button @click="alert()">保存</el-button></el-col>
                    <el-col :span="2"><el-button>重置</el-button></el-col>
            </el-row>
            </el-form>
        </el-col>
    </el-row>

    <!-- 搜索结果 -->
    <el-row>
        <el-col :span="24">
        <el-table
        :data="tableData"
        style="width: 100%">
            <el-table-column
                label="产品信息"
                width="300">
                <template slot-scope="scope">
                    <div class="rs-img">
                        <img src="../../assets/test.jpg" alt="">
                    </div>
                    <div style="float:left;">
                        <span>{{ scope.row.name }}</span>
                        <br>
                        <span>{{ scope.row.asin }}</span>
                        <br>
                        <span>{{ scope.row.price }}</span>
                    </div>
                    
                </template>
            </el-table-column>

            <el-table-column
                prop="brand"
                label="品牌"
                >
            </el-table-column>

            <el-table-column
                prop="rank"
                sortable
                label="Rank">
            </el-table-column>

            <el-table-column
                prop="reviews"
                sortable
                label="Reviews数／星级">
            </el-table-column>

            <el-table-column
                prop="seller"
                label="卖家">
            </el-table-column>

            <el-table-column
                prop="address"
                label="操作">
            </el-table-column>
        </el-table>
        </el-col>
        <el-col :span="9" :offset="15" style="margin-top:20px;">
        <el-pagination
            :current-page="currentPage"
            :page-sizes="[100, 200, 300, 400]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="400">
        </el-pagination>
        </el-col>
    </el-row>
</section>

</template>
<script>
export default {
  data () {
    return {
        isShow:false,
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        showbutton:{
            name:'更多'
        },
        form: { //查询条件
          country: '美国',
          date: '',
          minRank:'',
          maxRank:'',
          minReviews:'',
          maxReviews:'',
          sellerType:'1',
          asin:'',
          minSalesVolume:'',
          maxSalesVolume:'',
          minSalesAmount:'',
          maxSalesAmount:'',
          sellerId:'',
          minPrice:'',
          maxPrice:'',
          untitle:'',
          title:'',
          brand:'',
          minFollowSell:'',
          maxFollowSell:'',
          minStarLevel:'',
          maxStarLevel:''
        },
        options:[ //类目信息
            {
          value: 'zhinan',
          label: '指南',
          children: [{
            value: 'shejiyuanze',
            label: '设计原则',
            children: [{
              value: 'yizhi',
              label: '一致'
            }, {
              value: 'fankui',
              label: '反馈'
            }, {
              value: 'xiaolv',
              label: '效率'
            }, {
              value: 'kekong',
              label: '可控'
            }]
          }, {
            value: 'daohang',
            label: '导航',
            children: [{
              value: 'cexiangdaohang',
              label: '侧向导航'
            }, {
              value: 'dingbudaohang',
              label: '顶部导航'
            }]
          }]
        }
        ],
        selectedOptions: [], //站点选择数据
        selectedOptions2: [],
        tableData: [{ 
            img: 'test.jpg',
            name: '王小虎',
            asin: '0062110721',
            price:'2.25',
            brand:'N.A.',
            rank:'135',
            category:'Sports & Outdoors',
            reviews:'535',
            stars:'4.7',
            seller:'XiaoMing'
          },
          {
            img: 'test.jpg',
            name: '王小虎',
            asin: '0062110721',
            price:'2.25',
            brand:'N.A.',
            rank:'135',
            category:'Sports & Outdoors',
            reviews:'535',
            stars:'4.7',
            seller:'XiaoMing'
          },
          {
            img: 'test.jpg',
            name: '王小虎',
            asin: '0062110721',
            price:'2.25',
            brand:'N.A.',
            rank:'135',
            category:'Sports & Outdoors',
            reviews:'535',
            stars:'4.7',
            seller:'XiaoMing'
          }],
          currentPage: 1
    }
  },
  methods: {
      onSubmit() {
        console.log('submit!');
      },
      show(){
          this.isShow = !this.isShow
          if(this.showbutton.name === '更多'){
            this.showbutton.name = '收起'
          }else{
              this.showbutton.name = '更多'
          }
      },
      alert() {
        this.$prompt('请输入标签名称', '标签名称', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        //   inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        //   inputErrorMessage: '邮箱格式不正确'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '保存成功'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
        });
      }
    }
}
</script>


