<style lang="css" scoped>
    .el-menu a{
        text-decoration: none;
    }
</style>
<template>
<el-container>
    <el-aside style="width:200px;">
        <el-menu
        default-active="1"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose">
        <router-link to="/AssistantSelection/hot">
        <el-menu-item index="1">
            <i class="fa fa-fire"></i>
            <span slot="title">热卖排行榜</span>
        </el-menu-item>
        </router-link>
        <router-link to="/AssistantSelection/asin">
        <el-menu-item index="2">
            <i class="fa fa-line-chart"></i>
            <span slot="title">Asin排名查询</span>
        </el-menu-item>
        </router-link>
        </el-menu>
    </el-aside>
    <el-main>
        <router-view></router-view>
    </el-main>
</el-container>
</template>
<script>
export default {
  data() {
    return {
        activeIndex2: "1"
    }
  },
    methods: {
    handleOpen(key, keyPath) {
        console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
        console.log(key, keyPath);
    }
    }
};
</script>


