<style lang="css" scoped>
.el-header{
    background-color: #fff;
    border-bottom: 1px solid #ccc; 
    line-height: 60px;
}

.image{
    width: 100%;
}
.title{
    font-weight: bold;
    display: block;
    margin: 5px 0px;
}
.content{
    font-size:12px;
}
.totle{
    font-size:12px;
}
.totle i{
    font-weight: bold;
    color: red;
}
.badkywd{
  color: #409EFF;
  font-weight: bold;
  font-size:18px;
}
.review{
  font-size:12px;
}
.author{
    font-size:12px;
    font-weight: bold;
    margin-right: 10px;
}
.author i{
    color: #409EFF;
}
.reader{
    font-size:11px;
    color: #909399;
}
</style>
<template>
<el-row>
    <el-col :span="22" :offset="1">
        <el-container>
            <el-header>
                差评详情
            </el-header>
            <el-main>
                <el-row>
                    <el-col :span="4">
                        <el-card :body-style="{ padding: '0px' }">
                            <img src="../../assets/test.jpg" alt="" class="image">
                            
                            <div style="padding: 14px;">
                                <el-rate
                                v-model="stars"
                                disabled
                                show-score
                                text-color="#ff9900"
                                score-template="{value}">
                                </el-rate>
                                <span class="title">Jacquard Products 1 Ounce Arabic Gum</span>
                                
                                <span class="content"><strong>ASIN：</strong><i>B000WWK844</i></span>
                                <br>
                                <span class="content"><strong>价格：</strong> $ 4.8</span>
                                <br>
                                <span class="content"><strong>Reviews：</strong>72 (84%好评)</span><br>
                                <span class="content"><strong>差评：</strong>11 (16%差评)</span>
                                <div class="bottom clearfix">
                                <el-button type="text" icon="fa fa-mail-forward" class="button"> 在亚马逊中打开</el-button>
                                </div>
                            </div>
                        </el-card>
                    </el-col>
                    <el-col :span="19" :offset="1">
                        <el-card>
                            <div slot="header" class="clearfix">
                                <span class="totle">总差评数：<i>1</i></span>
                                <el-button style="float: right;" size="mini" type="primary" @click="dialogTableVisible = true">每日差评量</el-button>
                                <el-button style="float: right;margin-right:10px;" size="mini" @click="showtimes = true" type="primary">评价次出现率</el-button>
                                <el-dialog title="关键评论词统计" :visible.sync="dialogTableVisible">
                                    <el-form :model="form">
                                        <el-date-picker
                                        v-model="form.date"
                                        type="daterange"
                                        align="right"
                                        unlink-panels
                                        size="mini"
                                        range-separator="-"
                                        start-placeholder="开始日期"
                                        end-placeholder="结束日期"
                                        :picker-options="pickerOptions">
                                        </el-date-picker>
                                        <el-button type="primary" size="mini">查询</el-button>
                                    </el-form>
                                    <el-table :data="reviewsQuty" >
                                        <el-table-column property="date" align="center" label="日期"></el-table-column>
                                        <el-table-column property="quty" align="center" label="差评量"></el-table-column>
                                    </el-table>
                                </el-dialog>
                                <el-dialog title="每日差评量统计" :visible.sync="showtimes">
                                    <el-form :model="form">
                                        <el-date-picker
                                        v-model="form.date"
                                        type="daterange"
                                        align="right"
                                        unlink-panels
                                        size="mini"
                                        range-separator="-"
                                        start-placeholder="开始日期"
                                        end-placeholder="结束日期"
                                        :picker-options="pickerOptions">
                                        </el-date-picker>
                                        <el-button type="primary" size="mini">查询</el-button>
                                    </el-form>
                                    <el-table :data="reviewsQuty" >
                                        <el-table-column property="date" align="center" label="日期"></el-table-column>
                                        <el-table-column property="quty" align="center" label="差评量"></el-table-column>
                                    </el-table>
                                </el-dialog>
                            </div>
                            <el-table
                            :data="reviews">
                            <el-table-column
                            label="差评内容"
                            width="500">
                            <template slot-scope="scope">
                                <span class="badkywd">{{ scope.row.keyword }}</span>
                                <br>
                                <span class="review">{{ scope.row.review }}</span>
                                <br>
                                <span class="author">By : <i>{{ scope.row.author }}</i></span> 
                                <span class="reader">{{ scope.row.reader }}人觉得有用</span>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="星级"
                            align="center">
                            <template slot-scope="scope">
                                <el-rate
                                v-model="scope.row.stars"
                                disabled
                                show-score
                                text-color="#ff9900"
                                score-template="{value}">
                                </el-rate>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="日期"
                            align="center"
                            prop="date"
                            ></el-table-column>
                            <el-table-column
                            label="操作"
                            align="center">
                            <template slot-scope="scope">
                                <el-button size="mini" type="info" icon="fa fa-mail-forward"></el-button>
                            </template>
                            </el-table-column>
                            </el-table>
                        </el-card>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </el-col>
</el-row>

</template>
<script>
export default {
  data () {
    return {
        stars:4.2,
        dialogTableVisible:false,
        showtimes:false,
        form:{
            date:''
        },  
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },
        reviewsQuty:[{
            date:'2017-10-01',
            quty:1
        }],
        reviews:[{
            keyword:"Wouldn't buy again",
            review:"Super poor quality. Tried making ink from tea and it just clumped and wouldn't thicken my ink. Used 3/4 oz when recipe said use 1 tsp and it still didn't work.",
            author:'Tara / VP',
            reader:0,
            stars:2,
            date:'Sun Oct 01 2017'
        }],
        product:[{
            price:'4.8',
            reviews:'72',
            stars:'4.2',
            badReviews:'11'
        }]
    }
  }
}
</script>


