<style lang="css" scoped>
.title{
    text-align: center;
    font-weight: bold;
    color: #606266;
    font-size:14px;
}
.text{
    display: block;
    float: left;
    padding-left: 10px;
}
.content{
    text-align: center;
}
.content h4{
    font-size:15px;
    font-weight:normal;
    padding: 10px 0px;
}
.content span{
    border-bottom: 1px solid;
    cursor: pointer;
    font-size:40px;
}
.content span:hover{
    border-bottom: 1px solid #2D2F33 !important;
    color: #2D2F33 !important;
}
</style>
<template>
<section>
  <el-row>
    <el-col :span="22" :offset="1">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="最新监控时间">
          <el-date-picker
            v-model="formInline.time"
            type="daterange"
            align="right"
            unlink-panels
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions2"> 
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-input v-model="formInline.asin" placeholder="请输入ASIN/跟卖店铺"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
  <el-row type="flex" justify="space-around" style="margin-top:50px;">
    <el-col :span="4">
      <el-card class="box-card">
      <div slot="header" class="clearfix title">
          <span><i class="fa fa fa-shopping-basket" style="color:#409EFF;"></i> 被跟卖产品/跟卖者</span>
      </div>
          <div class="content">
              <span style="color:#409EFF;border-color:#409EFF;">1/0</span>
          </div>
      </el-card>
    </el-col>
    <el-col :span="4">
      <el-card class="box-card">
      <div slot="header" class="clearfix title">
          <span><i class="fa fa-gavel" style="color:#67C23A;"></i> 解决跟卖</span>
      </div>
          <div class="content">
              <span style="color:#67C23A;border-color:#67C23A;">0</span> <i>个</i>
          </div>
      </el-card>
    </el-col>
    <el-col :span="4">
      <el-card class="box-card">
      <div slot="header" class="clearfix title">
          <span><i class="fa fa-exclamation-triangle" style="color:#F56C6C;"></i> 高级预警</span>
      </div>
          <div class="content">
              <span style="color:#F56C6C;border-color:#F56C6C;">0</span> <i>封</i>
          </div>
      </el-card>
    </el-col>
    <el-col :span="4">
      <el-card class="box-card">
      <div slot="header" class="clearfix title">
          <span><i class="fa fa-check-square" style="color:#16a085;"></i> 已发邮件</span>
      </div>
          <div class="content">
              <span style="color:#16a085;border-color:#16a085;">0</span> <i>封</i>
          </div>
      </el-card>
    </el-col>
    <el-col :span="4">
      <el-card class="box-card">
      <div slot="header" class="clearfix title">
          <span><i class="fa fa-times-circle" style="color:#E6A23C;"></i> 发送失败</span>
      </div>
          <div class="content">
              <span style="color:#E6A23C;border-color:#E6A23C;">0</span> <i>封</i>
          </div>
      </el-card>
    </el-col>
  </el-row>
</section>
</template>
<script>
export default {
  data () {
    return {
      formInline:{
        time:'',
        asin:''
      },
      pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        }
    }
  }
}
</script>


