<style lang="css" scoped>
.el-menu a{
    text-decoration: none;
}
</style>
<template>
<el-container>
    <el-aside style="width:200px;">
        <el-menu
        default-active="1"
        class="el-menu-vertical-demo">
        <router-link to="/followsale/manage">
        <el-menu-item index="1">
            <i class="fa fa-camera-retro"></i>
            <span slot="title">跟卖管理</span>
        </el-menu-item>
        </router-link>
        <router-link to="/followsale/set">
        <el-menu-item index="2">
            <i class="fa fa-gear"></i>
            <span slot="title">跟卖设置</span>
        </el-menu-item>
        </router-link>
        <router-link to="/followsale/parameter">
        <el-menu-item index="3">
            <i class="fa fa-tasks"></i>
            <span slot="title">参数设置</span>
        </el-menu-item>
        </router-link>
        <router-link to="/followsale/template">
        <el-menu-item index="4">
            <i class="fa fa-file-text"></i>
            <span slot="title">模板设置</span>
        </el-menu-item>
        </router-link>
        </el-menu>
    </el-aside>
    <el-main>
        <router-view></router-view>
    </el-main>
</el-container>
</template>
<script>
export default {
  data () {
    return {
        defaultActive:'1'
    }
  }
}
</script>


